
.static .CodeMirror {
  height: initial;
}
.CodeMirror {
  text-align: left;
  font-size: 18px;
}
.CodeMirror.cm-s-dracula span.cm-variable-2 {
    color: #8be9fd;
}
.CodeMirror.cm-s-dracula span.cm-header {
    color: #42b983;
}
.CodeMirror.cm-s-dracula span.cm-em {
    font-style: normal;
    font-weight: bold;
}



