
#scenario-queue {
  background-color: #263238;
  overflow: hidden;
  background: #eaeef3;
  font-size: 1rem;
  width: 0;
  opacity: 0;
  -webkit-transition: width .33s ease-in-out, opacity .33s ease-in-out;
  transition: width .33s ease-in-out, opacity .33s ease-in-out;
}
#scenario-queue .title {
    cursor: pointer;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -ms-flex-item-align: end;
        align-self: flex-end;
    font-size: .8em;
    background: #263238;
    padding: .15em .75em;
    color: #eaeef3;
}
#scenario-queue .title .play {
      font-size: .65em;
      margin-left: .65em;
}
#scenario-queue .title, #scenario-queue .scenario {
    opacity: 1;
    visibility: visible;
    -webkit-transition: opacity 1s ease;
    transition: opacity 1s ease;
}
#scenario-queue .wrapper {
    width: 0;
    padding: .5em;
    height: 95vh;
    overflow-y: auto;
    overflow-x: hidden;
    position: fixed;
    top: 0;
    right: 0;
}
#scenario-queue.expanded {
    width: 320px;
    opacity: 1;
}
#scenario-queue.expanded .wrapper {
      width: 305px;
}
#scenario-queue.minimized {
    width: 37px;
    opacity: 1;
}
#scenario-queue.minimized .wrapper {
      width: 35px;
}
#scenario-queue.minimized .title, #scenario-queue.minimized .scenario {
      opacity: 0;
      visibility: hidden;
}
#scenario-queue .header {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 2em;
    position: relative;
    height: 75px;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
#scenario-queue .header .icon {
      cursor: pointer;
      position: absolute;
      top: 0;
      right: 0;
      font-size: 1.25rem;
}
#scenario-queue .scenario {
    border-bottom: 1px solid #000;
    text-align: left;
    cursor: pointer;
}
#scenario-queue .scenario span {
      width: 250px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      display: inline-block;
}
#scenario-queue .scenario i {
      float: right;
}
#scenario-queue .scenario i.success {
        color: green;
}
#scenario-queue .scenario i.error {
        color: red;
}



