
body {
  margin: 0;
  min-height: 100vh;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  font-size: 32px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  min-height: 100vh;
}
#app .content {
    -webkit-box-flex: 1;
        -ms-flex: 1 1;
            flex: 1 1;
    padding: 0 .5em 3em;
}
#app .utility-bar {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
#app code.header {
    white-space: normal;
}
.buttons {
  margin-top: 10px;
  width: 100%;
  text-align: right;
}
.buttons button {
    cursor: pointer;
    background-color: #263238;
    color: white;
    padding: 10px;
    width: 150px;
    border: none;
}
.buttons button:hover {
      color: #8be9fd;
}



